.nav-home {
    ol {
        margin: 0;
        padding: 10px 0 20px;
    }
    li {
        list-style: none;
        display: inline-block;
        vertical-align: top;

        &:last-child {
            float: right;
        }
    }
    a {
        display: block;
        text-decoration: none;
        color: #444;
    }

    .nav-home__item {
        height: 20px;
        padding: .5em;

        &:hover,
        &:active,
        &.nav-home__item_is-active {
            border-bottom: .6em solid $color-ae-light;
        }
    }

    .nav-home__item_github {
        width: 20px;
        height: 20px;
        background: // static/img/github-logo.svg
            url('data:image/svg+xml;base64,')
            center center
            no-repeat;

        &:hover,
        &:active {
            border-bottom: 0;
        }
    }
}
